.container-fluid {
  min-height: 100vh;
  text-align: center;
  padding: 2rem;
  background-color: #61efff;
}

.container {
  min-height: 80vh;
  width: 30rem;
  padding: 2.2rem;
  border-radius: 1.5rem;
  background: #f5f5f5;
  box-shadow: 1px 1px 15px 1px rgba(31, 31, 31, 0.236);
}

.innertext {
  z-index: 2;
}

.heading {
  font-family: "Sora", sans-serif;
}
.heading h1 {
  font-weight: 700;
}

.inputsection {
  display: flex;
  position: relative;
  top: 1rem;
  justify-content: space-between;
  border: 2px solid #0000ff;
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: 500ms;
}

.inputsection:hover {
  display: flex;
  position: relative;
  top: 1rem;
  justify-content: space-between;
  border: 2px solid #0000ff;
  padding: 0.5rem;
  border-radius: 0.5rem;
  box-shadow: 1px 1px 3px 2px rgba(31, 31, 31, 0.236);
}

.inputtext {
  display: inline-flex;
}

.inputbtn {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  background-color: #0000ff;
  color: white;
  border: none;
  transition: 500ms;
}

.inputbtn:hover {
  background-color: #0000ff;
  color: black;
  border: none;
  box-shadow: 1px 1px 3px 2px rgba(31, 31, 31, 0.236);
}

input[type="text"] {
  font-family: "Sora", sans-serif;
  font-size: 1.3rem;
  background-color: rgba(255, 255, 255, 0.2);
  vertical-align: middle;
  height: 3rem;
  width: 20rem;
  font-weight: 400;
  border-radius: 0.5rem;
  border: none;
}

input[type="text"]:focus {
  font-family: "Sora", sans-serif;
  font-size: 1.3rem;
  background-color: rgba(255, 255, 255, 0.2);
  vertical-align: middle;
  height: 3rem;
  width: 20rem;
  font-weight: 400;
  border-radius: 0.5rem;
  outline: none;
}

.listitems {
  display: flex;
  margin-top: -1.5rem;
  border: 0.1rem solid white;
  justify-content: space-between;
  background-color: white;
  padding: 0.5rem;
  border-radius: 0.5rem;
  box-shadow: 1px 1px 3px 2px rgba(31, 31, 31, 0.236);
}

.listcontainer {
  position: relative;
  top: 4rem;
}

.itemstext {
  width: 16rem;
  position: relative;
  top: 0.5rem;
  font-size: 1.3rem;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  text-align: left;
}

.deletebtn {
  position: relative;
  bottom: 3.5rem;
  left: 3.5rem;
  border: none;
  margin-left: 1rem;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  background-color: red;
  color: white;
  transition: 500ms;
}

.deletebtn:hover {
  background-color: red;
  color: black;
  box-shadow: 1px 1px 3px 2px rgba(31, 31, 31, 0.236);
}
.editbtn {
  position: relative;
  bottom: 3.5rem;
  left: 3.5rem;
  border: none;
  margin-left: 10rem;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  background-color: rgb(255, 140, 0);
  color: white;
  transition: 500ms;
}

.editbtn:hover {
  background-color: rgb(255, 140, 0);;
  color: black;
  box-shadow: 1px 1px 3px 2px rgba(31, 31, 31, 0.236);
}

.removeallbtn {
  margin-top: 4rem;
}

.resetbtn {
  background-color: #0000ff;
  color: white;
  font-family: "Sora", sans-serif;
  font-weight: 700;
  transition: 500ms;
  border: none;
}

.resetbtn:hover {
  background-color: #0000ff;
  color: black;
  box-shadow: 1px 1px 3px 2px rgba(31, 31, 31, 0.236);
  border: none;
}
